.navbar {
  height: 60px;
  background-color: #72a9be;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.navbar-logo {
  display: flex;
  width: 30vw;
  text-align: start;
  margin-left: 5vw;
}
.navbar-list {
  width: 70vw;
  display: flex;
  list-style: none;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
  margin-right: 10vw;
  gap: 20px;
}

.nav-item {
  border-radius: 10px;
  text-decoration: none;
  padding: 0.45rem 1rem;
  color: black;
}

.nav-item-block {
  width: 0px;
  height: 0px;

  background-color: #cbf8ff;
  border-radius: 5px;
  transition: all 0.5s;

  display: flex;
  margin: auto;

  position: relative;
  top: 5px;
}

.nav-item:hover ~ .nav-item-block {
  width: 70px;
  height: 5px;

  box-shadow: 0 0 15px #035397;
}
